<template>
  <div v-if="item" class="profile">
    <div
      class="nav-background"
      :style="{ 'background-image': `url(${item.background})` }"
    >
      <b-avatar class="avatar" :src="item.avatar" />
    </div>
    <div
      class="content d-flex justify-content-center flex-column flex-fill pt-4"
    >
      <div class="title-info d-flex justify-content-between">
        <span> {{ item.email }} </span>
        <div class="d-flex">
          <span> {{ locationInfo }} </span>
        </div>
      </div>
      <div class="avatar-info d-flex justify-content-center">
        <span class="block">100 читателей</span>
        <span class="name">{{ item.name }}</span>
        <span class="block">500 тыс. лайков</span>
      </div>
      <div class="d-flex justify-content-center">
        <span
          class="description"
          v-if="description && description != ' '"
          v-html="description"
        >
        </span>
      </div>
      <b-row class="profile-controls d-flex justify-content-center mt-3">
        <b-col v-if="id" xl="3" class="d-flex justify-content-center">
          <b-button v-if="!hasSubscribe" @click="addSubscribe(id)">
            <span class="mr-2">Подписаться</span>
          </b-button>
          <b-button v-else variant="danger" @click="deleteSubscribe(id)">
            <span class="mr-2">Отписаться</span>
          </b-button>
        </b-col>
        <!-- <b-col 
                    v-if="id"
                    xl="3"
                    class="d-flex justify-content-center"                
                >
                    <b-button>
                        Связаться со мной
                    </b-button>
                </b-col> -->
        <b-col v-if="!id" xl="3" class="d-flex justify-content-center">
          <b-button @click="toProfileEdit"> Изменить профиль </b-button>
        </b-col>
      </b-row>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "Profile",

  props: ["id"],

  data() {
    return {
      item: {},
      hasSubscribe: false,
    };
  },

  mounted() {
    this.loadProfileItem();
  },

  watch: {
    id() {
      this.loadProfileItem();
    },
  },

  computed: mapState({
    profile: (state) => state.profileItem,
    locationInfo() {
      return `${this.item.country} ${this.item.town ? "," : ""} ${
        this.item.town
      }`;
    },
    description() {
      return this.item.description;
    },
  }),

  methods: {
    toProfileEdit() {
      this.$router.push({ name: "EditProfile" });
    },

    loadProfileItem() {
      this.$startLoading();
      this.$store
        .dispatch("profileItem")
        .then(() => {
          if (!this.id) {
            this.item = this.profile;
          } else {
            this.hasSubscribe = this.profile.subscriptions.includes(this.id);
          }
        })
        .finally(() => {
          this.$stopLoading();
        });
      if (this.id) {
        this.$startLoading();
        this.$store
          .dispatch("profileItemById", this.id)
          .then((response) => {
            this.item = response;
          })
          .finally(() => {
            this.$stopLoading();
          });
      }
    },

    addSubscribe(id) {
      this.$startLoading();
      this.$store.dispatch("addSubscribe", id).finally(() => {
        this.$stopLoading();
      });
      this.hasSubscribe = true;
    },

    deleteSubscribe(id) {
      this.$startLoading();
      this.$store.dispatch("deleteSubscribe", id).finally(() => {
        this.$stopLoading();
      });
      this.hasSubscribe = false;
    },
  },
};
</script>

<style lang="scss" scoped>
.nav-background {
  height: 200px;
  display: flex;
  justify-content: center;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  .avatar {
    position: relative;
    top: 100px;
    width: 250px;
    height: 250px;
  }
}

.title-info {
  font-size: 20px;
}

.avatar-info {
  padding-top: 100px;
  span {
    display: flex;
    align-items: center;
  }
  .name {
    font-size: 26px;
    margin: 0 50px;
  }
  .block {
    padding: 4px 15px;
    color: #ffffff;
    background: var(--primary-color);
    border-radius: 17px;
  }
}

.title-info,
.name {
  color: var(--text-color);
}

.description {
  margin-top: 30px;
  font-size: 16px;
  min-width: 250px;
  max-width: 600px;
  border: 2px solid var(--primary-color);
  border-radius: 15px;
  padding: 10px 10px 0;
}

.profile-controls {
  div {
    margin-top: 20px;
  }

  button {
    min-width: 150px;
    width: 300px;
  }
}

@media (max-width: 800px) {
  .avatar {
    width: 150px !important;
    height: 150px !important;
  }
  .nav-background {
    height: 150px;
  }

  .title-info {
    padding-top: 75px;
  }
}
</style>
